@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body{padding:0;margin:0;font-family:'Raleway', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#222831;color:#ececec}.book-card svg{vertical-align:unset}.sign-out{cursor:pointer}.sign-out svg{vertical-align:middle}.error{color:#ff0000;font-size:0.8rem}.book{background-color:#1c2129}.book .card-img-top{height:12rem;object-fit:cover;-webkit-transform:scale(0.9);transform:scale(0.9)}.book-modal .modal-content{background-color:#1c2129}.book-modal .modal-content .modal-header .close span{color:#ececec}.book-image-group p{margin-bottom:0.5rem}.book-image-group label{cursor:pointer;display:block;width:100%;height:calc(1.5em + .75rem + 2px);padding:.375rem .75rem;font-size:1rem;font-weight:400;line-height:1.5;color:#495057;background-color:#fff;background-clip:padding-box;border:1px solid #ced4da;border-radius:.25rem;transition:border-color .15s ease-in-out,box-shadow .15s ease-in-out}.book-image-group input{display:none}.book-image img{height:10rem}.fixed-bottom{position:fixed;bottom:0}

